var React = require('react');
var Formsy = require('formsy-react');
var _ = require('underscore');
var FormsyItem = require('../../../../form/Form-item-base');
var TemplatesRegistry = require('../TemplatesRegistry');
var Category  = require('../common/category');
var Radios = require('../../../../form/form-item-radios');
var NavTab = require('../../../../nav-tab/nav-tab');
var Priority = require('../common/priority');
require('../../../../form/form.less');
var Select = require('../../../../select/form-item-select.jsx');
var Uploader = require('../../../../uploader/uploader');
var API = require('../../../../api');
/**
 * @type {__React.ClassicComponentClass<P>}
 */
var BaseInfo = React. createClass({
  propTypes: {
    onChange: React.PropTypes.func,
    data: React.PropTypes.object,
  },
  getDefaultProps() {
    return {
      data: {}
    }
  },
  getInitialState() {
    return {
      categoryData: {},
      bookFile: {},
      file: {name: '', url: '/uploads/logo.png'},
      priorityArray: [],
    };
  },
  onOtherSelect(item) {
    
  },
  onCategoryChange(categoryData) {
    this.setState({
      categoryData: categoryData,
    });
  },
  onBookFileUploaded(fileArray) {
    this.setState({
      bookFile: fileArray
    });
  },
  onFileUploaded(fileArray) {
    this.setState({
      file: file,
    });
  },
  onSubmitCheckChange(options) {
  },
  submit(model) {

  },
  onFormChange() {
    var { onChange, data } = this.props;
    var model = this.refs.form.getModel();
    onChange(_.assign(data, model));
  },
  onPriorityChange(priorityArray) {
    var { data, onChange } = this.props;
    data.priorityArray = priorityArray;
    onChange(data);
  },
  render() {
    var { file, bookFile } = this.state;
    var { data } = this.props;

    var clarificaitonbook = data.clarificaitonbook;
    var categoryData = {
      hasProject: clarificaitonbook.hasClarificaitonbook,
      hasPicture: clarificaitonbook.hasPicture,
      hasClarificaitonbook: clarificaitonbook.hasClarificaitonbook
    };
    return (<div>
      <Formsy.Form onValid={this.onFormChange}
        ref="form" className="base-info-form"
      >
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix small">
              <label></label>
              <div className="Form-item-base clearfix">
                <Category onChange={this.onCategoryChange} data={categoryData}/>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6">
            <div className="Form-item clearfix small">
              <label></label>
              <FormsyItem
                name="clarificaitonName"
                required tips=""  placeholder="" validations="minLength:1"
                validationErrors="" value={clarificaitonbook.name}
              />
            </div>
          </div>
          {categoryData.hasClarificaitonbook ?
            <div className="col-sm-6">
              <div className="Form-item clearfix small">
                <label></label>
                <div className="Form-item-base">
                  <Uploader
                    uploadUrl={API.uploadFile} onFileUploaded={this.onBookFileUploaded}
                    btnText="" data={{name: bookFile[0] && bookFile[0].file_name}} name="bookFile"
                  />
                </div>
              </div>
            </div> : null
          }
        </div>
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix">
              <label></label>
              <div className="Form-item-base">
                <Uploader
                  uploadUrl={API.uploadMultipleFile} onFileUploaded={this.onFileUploaded}
                  btnText="" data={file} name="otherFile"
                />
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="clarificaiton-remark" type="textarea" required tips="" />
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="submitCheck">
                <Radios options={[{value: "1", label: '', checked: true}, {value: "0", label: ''}]}/>
              </FormsyItem>
            </div>
          </div>
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="advancedPublic">
                <Radios options={[{value: "1", label: '', checked: true}, {value: "0", label: ''}]}/>
              </FormsyItem>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="timeLimit" value={data.timeLimit || ''}/>
            </div>
          </div>
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="cutFee">
                <Select
                  options={[
                    {value: 0, label: ''},
                    {value: 5, label: ' 85%'},
                    {value: 6, label: ' 70%'},
                  ]} value={0}
                />
              </FormsyItem>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="isRisk">
                <Select
                  options={[{value: 0, label: ''}, {value: 1, label: ''}, {value: 2, label: ''}]}
                  value={0}
                />
              </FormsyItem>
            </div>
          </div>
          <div className="col-sm-6">
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="rightItemNum" value={''}/>
            </div>
          </div>
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="specificationPageNum" value={''} />
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="remark" type="textarea" required tips="" />
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-6">
            <div className="Form-item clearfix">
              <label></label>
              <FormsyItem name="isPriority">
                <Radios options={[{value: "1", label: '', checked: true}, {value: "0", label: ''}]}/>
              </FormsyItem>
            </div>
          </div>
        </div>
      </Formsy.Form>
      <div className="row">
        <div className="col-sm-12">
          <Priority onChange={this.onPriorityChange} data={data.priority}/>
        </div>
      </div>
    </div>);
  }
});

module.exports = BaseInfo;
