var React = require('react');
var Formsy = require('formsy-react');
var InputBase = require('../../../../form/Form-item-base');
require('../../../../form/form.less');
var Select = require('../../../../select/select-base');

var FormSelect = React.createClass({
  propTypes: {
    handleFocus: React.PropTypes.func,
    handleBlur: React.PropTypes.func,
  },
  onToggleShow(show) {
    var { handleFocus, handleBlur } = this.props;
    if (show) {
      handleFocus();
    } else {
      handleBlur();
    }
  },
  render() {
    return (<div>
      <Select data={[{id:1, name: 1}, {id:2, name: 2}]} onSelect={this.onOtherSelect} onToggleShow={this.onToggleShow}/>
    </div>);
  }
});

var BaseInfo = React.createClass({
  onOtherSelect(item) {
    
  },
  render() {
    return (<div>
      <Formsy.Form onSubmit={this.submit} ref="form">
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix small">
              <label></label>
              <div className="Form-item-base clearfix">
                <label htmlFor=""><input type="checkbox" name="" id=""/></label>
                <label htmlFor=""><input type="checkbox" name="" id=""/></label>
                <label htmlFor=""><input type="checkbox" name="" id=""/></label>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix small">
              <label></label>
              <InputBase name="pwd" required tips=""  placeholder=""/>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix">
              <label></label>
              <InputBase name="other" required validations="isInt" tips="">
                <FormSelect />
              </InputBase>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-sm-12">
            <div className="Form-item clearfix">
              <label></label>
              <InputBase name="pwd" type="textarea" required tips="" />
            </div>
          </div>
        </div>
      </Formsy.Form>

    </div>);
  }
});

module.exports = BaseInfo;


